import React from 'react'
import '../css/ContactAPI.css';
function ContactAPI()
{
    return(
        <div className="contact">
            <div className="contact-top">
                <h1>Thanks for your time. How can I help You today?</h1>
            </div>
            <div className="contact-mid">
                <div className="contact-input"><label>Name</label><input type="text"></input></div>
                <div className="contact-input"><label>Email</label><input type="text"></input></div>
            </div>
            <div className="contact-bot">
            <div className="contact-input"> <label>Message</label><textarea type="text" placeholder="CONTACT API UNDER CONSTRUCTION."></textarea></div>
            <button>Submit</button>
           </div>
        </div>
    )
}


export default ContactAPI