import React from 'react'
import "../css/Projects.css"
const Projects = props =>(
      
            <div className="project">
                <h1>{props.title}</h1>
                <p>{props.des}</p>
                <img src={props.image} alt={props.alt}></img>
                <a href={props.link} target="_blank">Visit Website</a>
            </div>   
    
);
 
export default Projects